import React, { useState, useEffect } from 'react';
import FilterExpenses from './Expenses/FilterExpenses';
import FilterByDate from './FilterByDate';
import TypeWritterEffect from './UI/TypewritterEffect';
import Theme from './UI/Theme';
import "./Header.css";

const Header = (props) => {
  const [HeaderTheme, setHeaderTheme] = useState('');
  const [HeaderAbout, setHeaderAbout] = useState(0);
  var i = 0;
  const txt = ` Hi! We are team Binary Wizards  ( the group of Web Developers and Machine learning enthusiasts ).
Currently We are in V semester. We are aiming to provide you best service so that these services can help you. 
   Currenty We are working in differents project's.  We will provide you other services very soon. You can share your feedback via
   linkedln.`;
  var speed = 85;
  function typeWriter() {
    if (i < txt.length) {
      document.getElementById("demo").innerHTML += txt.charAt(i);
      i++;
      setTimeout(typeWriter, speed);
    }
  }


  const AboutUsClickHandler = () => {
    if (HeaderAbout == 0) {
      setHeaderAbout(1);
      props.FromAppForAbout(1);
      document.getElementById("demo").innerHTML = '';
      i = 0;
      typeWriter();
    }
    else {
      setHeaderAbout(0);
      props.FromAppForAbout(0);
      document.getElementById("demo").innerHTML = '';

    }


  }
  const homeHandler = () => {
    setHeaderAbout(0);
    props.FromAppForHome();
    i = 0;
  }


  useEffect(() => {

    props.defaultTheme === 'dark-theme' ?
      setHeaderTheme("dark-theme") : setHeaderTheme("light-theme");
  }, [props.defaultTheme])


  const FilterAndSaveInHeader = (filterExpenseName) => {
    props.FromAppForFilter(filterExpenseName);
  }


  const SaveThemeInHeader = () => {
    props.FromApp();
  }
  const FilterDateAndSaveInHeader = (s, e) => {
    props.FromAppForDateFilter(s, e);
  }

  if (HeaderTheme == "light-theme") {

    if (HeaderAbout != 1)
      return (
        <>
          <nav className="navbar navbar-expand-lg fixed-top lightNavbar  ">
            <div className="container-fluid">
              
                <img src={require("../image/logo.png")} alt="KharchaPani-logo" className='KharchaPani-logo'  />

             
        
              <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                  <li className="nav-item mx-2">
                    <a className="nav-link active" aria-current="page" href="#" onClick={homeHandler}>Home</a>
                  </li>

                  <li className="nav-item dropdown">
                    <a className="nav-link dropdown-toggle mx-2" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                      <i className="fa fa-filter"></i> Date Filter
                    </a>
                    <ul className="dropdown-menu">
                      <FilterByDate FromHeaderForDateFilter={FilterDateAndSaveInHeader} />
                    </ul>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link mx-2" href="#" onClick={AboutUsClickHandler}>About Us</a>
                  </li>
                  <li className="nav-item dropdown mx-2">
                    <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                      Follow Us
                    </a>
                    <ul className="dropdown-menu  followBox">
                      <li className="nav-item ">
                        <a className="nav-link mx-2 text-dark follow" href="https://www.linkedin.com/in/akash-gupta-9a2461228">Akash Gupta</a>
                      </li>
                      <div class="dropdown-divider"></div>
                      <li className="nav-item ">
                        <a className="nav-link mx-2 text-dark follow" href="https://www.linkedin.com/in/abhay-gupta-b87624226">Abhay Gupta</a>
                      </li>
                    </ul>
                  </li>

                </ul>

                <Theme FromHeader={SaveThemeInHeader} defaultHeaderTheme={props.defaultTheme} />
                <FilterExpenses FromHeader={FilterAndSaveInHeader} searchingTitle={props.searchingTitleFromApp} />
              </div>
            </div>
          </nav>

          <div style={{ display: 'none' }} className='demoBox'>

            <div id='demo' ></div>
          </div>
        </>
      )
    else return (
      <>
        <nav className="navbar navbar-expand-lg fixed-top lightNavbar  ">
          <div className="container-fluid">
          <img src={require("../image/logo.png")} alt="KharchaPani-logo" className='KharchaPani-logo'  />
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item mx-2">
                  <a className="nav-link active" aria-current="page" href="#" onClick={homeHandler}>Home</a>
                </li>

                <li className="nav-item dropdown">
                  <a className="nav-link dropdown-toggle mx-2" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="fa fa-filter"></i> Date Filter
                  </a>
                  <ul className="dropdown-menu">
                    <FilterByDate FromHeaderForDateFilter={FilterDateAndSaveInHeader} />
                  </ul>
                </li>
                <li className="nav-item">
                  <a className="nav-link mx-2" href="#" onClick={AboutUsClickHandler}>About Us</a>
                </li>

                <li className="nav-item dropdown mx-2">
                  <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    Follow Us
                  </a>
                  <ul className="dropdown-menu followBox">
                    <li className="nav-item ">
                      <a className="nav-link mx-2 text-dark follow" href="https://www.linkedin.com/in/akash-gupta-9a2461228">Akash Gupta</a>
                    </li>
                    <div class="dropdown-divider"></div>
                    <li className="nav-item ">
                      <a className="nav-link mx-2 text-dark follow" href="https://www.linkedin.com/in/abhay-gupta-b87624226">Abhay Gupta</a>
                    </li>
                  </ul>
                </li>

              </ul>

              <Theme FromHeader={SaveThemeInHeader} defaultHeaderTheme={props.defaultTheme} />
              <FilterExpenses FromHeader={FilterAndSaveInHeader} searchingTitle={props.searchingTitleFromApp} />
            </div>
          </div>
        </nav>
        < TypeWritterEffect />
        <div style={{ display: 'block' }} className='demoBox'>
          <div id='demo' style={{ color: 'var(--bgcolvar)' }}></div>
        </div>
      </>
    );

  }
  else {
    if (HeaderAbout != 1)
      return (
        <>
          <nav className="navbar navbar-expand-lg bg-dark fixed-top navbar-dark">
            <div className="container-fluid">
            <img src={require("../image/logo.png")} alt="KharchaPani-logo" className='KharchaPani-logo'  />
              <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
              </button>
              <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                  <li className="nav-item mx-2">
                    <a className="nav-link active" aria-current="page" href="#" onClick={homeHandler}>Home</a>
                  </li>
                  <li className="nav-item dropdown mx-2">
                    <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                      <i className="fa fa-filter"></i> Date Filter
                    </a>
                    <ul className="dropdown-menu">
                      <FilterByDate FromHeaderForDateFilter={FilterDateAndSaveInHeader} />
                    </ul>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link mx-2" href='#' onClick={AboutUsClickHandler}>About Us</a>
                  </li>

                  <li className="nav-item dropdown mx-2">
                    <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                      Follow Us
                    </a>
                    <ul className="dropdown-menu followBox">
                      <li className="nav-item ">
                        <a className="nav-link mx-2 text-light follow" href="https://www.linkedin.com/in/akash-gupta-9a2461228">Akash Gupta</a>
                      </li>
                      <div class="dropdown-divider"></div>
                      <li className="nav-item ">
                        <a className="nav-link mx-2 text-light follow" href="https://www.linkedin.com/in/abhay-gupta-b87624226">Abhay Gupta</a>
                      </li>
                    </ul>
                  </li>

                </ul>

                <Theme FromHeader={SaveThemeInHeader} defaultTheme={props.defaultTheme} />
                <FilterExpenses FromHeader={FilterAndSaveInHeader} searchingTitle={props.searchingTitleFromApp} />
              </div>
            </div>
          </nav>
          <div style={{ display: 'none' }} className='demoBox'>
            <div id='demo' style={{ fontSize: '20px' }}></div>
          </div>
        </>
      );
    else return (
      <>
        <nav className="navbar navbar-expand-lg bg-dark fixed-top navbar-dark">
          <div className="container-fluid">
          <img src={require("../image/logo.png")} alt="KharchaPani-logo" className='KharchaPani-logo'  />
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarSupportedContent">
              <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                <li className="nav-item mx-2">
                  <a className="nav-link active" aria-current="page" href="#" onClick={homeHandler}>Home</a>
                </li>
                <li className="nav-item dropdown mx-2">
                  <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="fa fa-filter"></i> Date Filter
                  </a>
                  <ul className="dropdown-menu">
                    <FilterByDate FromHeaderForDateFilter={FilterDateAndSaveInHeader} />
                  </ul>
                </li>
                <li className="nav-item">
                  <a className="nav-link mx-2" href='#' onClick={AboutUsClickHandler}>About Us</a>
                </li>
                <li className="nav-item dropdown mx-2">
                  <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                    Follow Us
                  </a>
                  <ul className="dropdown-menu followBox">
                    <li className="nav-item ">
                      <a className="nav-link mx-2 text-light follow" href="https://www.linkedin.com/in/akash-gupta-9a2461228">Akash Gupta</a>
                    </li>
                    <div class="dropdown-divider"></div>
                    <li className="nav-item ">
                      <a className="nav-link mx-2 text-light follow" href="https://www.linkedin.com/in/abhay-gupta-b87624226">Abhay Gupta</a>
                    </li>
                  </ul>
                </li>

              </ul>

              <Theme FromHeader={SaveThemeInHeader} defaultTheme={props.defaultTheme} />
              <FilterExpenses FromHeader={FilterAndSaveInHeader} searchingTitle={props.searchingTitleFromApp} />
            </div>
          </div>
        </nav>
        < TypeWritterEffect />
        <div style={{ display: 'block' }} className='demoBox'>
          <div id='demo' style={{ color: 'var(--bgcolvar)' }}></div>
        </div>
      </>
    );
  }
}
export default Header;