import React from "react";
import { useTypewriter, Cursor } from 'react-simple-typewriter';
import './TypewritterEffect.css';

// npm i react-simple-typewriter


const TypeWritterEffect = () => {
    const [text, helper] = useTypewriter({
      words: ['team Binary Wizards.','Web Developers.', 'Machine learning enthusiasts.', 'Problem Solver.'],
      loop: 2,
      typeSpeed:95,
              deleteSpeed:80,
              delaySpeed:1000,
    });
    const { isType, isDelete, isDelay, isDone } = helper
    return (
      <p className="basicText">We are {' '}
        <span style={{ fontWeight: 'bold', color: 'green' }}>{text}
        </span>
        <span style={{
         color:
        'red'}}><Cursor cursorStyle='|' /></span>
      </p>
    );
  };
  export default TypeWritterEffect;
  